/**
 * Get default image URL based on cuisine type
 *         
 */
export const getDefaultRestaurantImage = (cuisineType, restaurantName = '') => {
  // Map cuisine types to appropriate Unsplash images
  //     Unsplash 
  
  const imageMap = {
    '': 'https://images.unsplash.com/photo-1571091718767-18b5b1457add?w=800&h=600&fit=crop',
    '': 'https://images.unsplash.com/photo-1555939594-58d7cb561ad1?w=800&h=600&fit=crop',
    '': 'https://images.unsplash.com/photo-1517248135467-4c7edcad34c4?w=800&h=600&fit=crop',
    '': 'https://images.unsplash.com/photo-1579584425555-c3ce17fd4351?w=800&h=600&fit=crop',
    '': 'https://images.unsplash.com/photo-1563379091339-03246963d4d0?w=800&h=600&fit=crop',
    ' ': 'https://images.unsplash.com/photo-1565299624946-b28f40a0ca4b?w=800&h=600&fit=crop',
    '': 'https://images.unsplash.com/photo-1558030006-450675393462?w=800&h=600&fit=crop',
    '': 'https://images.unsplash.com/photo-1555507036-ab1f4038808a?w=800&h=600&fit=crop',
    ' ': 'https://images.unsplash.com/photo-1559339352-11d035aa65de?w=800&h=600&fit=crop',
    '': 'https://images.unsplash.com/photo-1555396273-367ea4eb4db5?w=800&h=600&fit=crop',
  }

  // Try to get image by cuisine type
  //       
  if (cuisineType && imageMap[cuisineType]) {
    return imageMap[cuisineType]
  }

  // Fallback: Use restaurant name to determine image
  // :     
  const name = restaurantName.toLowerCase()
  
  if (name.includes('') || name.includes('')) {
    return imageMap['']
  }
  if (name.includes('') || name.includes('')) {
    return imageMap['']
  }
  if (name.includes('') || name.includes('') || name.includes('')) {
    return imageMap[' ']
  }
  if (name.includes('') || name.includes('')) {
    return imageMap['']
  }
  if (name.includes('') || name.includes('')) {
    return imageMap['']
  }
  if (name.includes('')) {
    return imageMap['']
  }
  if (name.includes('') || name.includes('')) {
    return imageMap[' ']
  }
  if (name.includes('')) {
    return imageMap['']
  }

  // Default: Moroccan/Arabic restaurant image
  // :   /
  return imageMap[''] || 'https://images.unsplash.com/photo-1555939594-58d7cb561ad1?w=800&h=600&fit=crop'
}

